import React, { useState } from 'react'
import { userRegister } from '../../../api/users'
import LabelInput from '../../../components/Forms/LabelInput'

export default function RegisterForm({ changeMode }) {
  const [firstName, setFirstName] = useState('')
  const [secondName, setSecondName] = useState('')
  const [fatherName, setFatherName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [errorMessage, setErrorMessage] = useState('')

  const handleFormSubmit = async (e) => {
    e.preventDefault()
    const response = await userRegister(firstName, secondName, fatherName, email, password)
    if (!response) {
      setErrorMessage(' .     email  .')
    }
  }

  return (
    <>
      <h3 className="text-xl mb-4"> </h3>
      <form action="" className="flex flex-col gap-2" onSubmit={(e) => handleFormSubmit(e)}>
        <LabelInput
          label="'"
          name="first_name"
          value={firstName}
          id="first_name"
          placeholder="  '"
          type="text"
          required={true}
          setValue={setFirstName}
        />

        <LabelInput
          label=""
          name="second_name"
          value={secondName}
          id="second_name"
          placeholder="  "
          type="text"
          required={true}
          setValue={setSecondName}
        />

        <LabelInput
          label=" "
          name="father_name"
          value={fatherName}
          id="father_name"
          placeholder="   "
          type="text"
          required={true}
          setValue={setFatherName}
        />

        <LabelInput
          label="Email"
          name="email"
          value={email}
          id="email"
          placeholder="  email"
          type="email"
          required={true}
          setValue={setEmail}
        />
        <LabelInput
          label=""
          name="password"
          value={password}
          id="password"
          placeholder="  "
          type="password"
          required={true}
          setValue={setPassword}
        />

        <input
          type="submit"
          id="register-button"
          value=""
          className="transition-colors duration-300 ease-in-out cursor-pointer bg-black border border-black text-white rounded-full py-3 mt-8 hover:bg-transparent hover:text-black"
        />

        {errorMessage && <div className="text-red-500 bg-red-100 px-4 py-2 rounded">{errorMessage}</div>}
      </form>

      <div className="flex items-center w-full py-2 my-8">
        <div className="flex-grow border-t border-gray-400"></div>
        <span className="px-4 text-sm text-gray-600">  </span>
        <div className="flex-grow border-t border-gray-400"></div>
      </div>

      <div className="flex justify-center">
        <button
          className="transition-colors ease-out duration-300 hover:text-indigo-500 underline text-xl"
          onClick={() => changeMode()}>
          
        </button>
      </div>
    </>
  )
}
